/* circle at top left */
@-webkit-keyframes ball1 {
    0%   {top: -30vh;}
    50%  {top: 8vh;}
    100% {top: -30vh;}
}
@keyframes ball1 {
    0%   {top: -30vh;}
    50%  {top: 8vh;}
    100% {top: -30vh;}
}
@-webkit-keyframes ball3 {
    0%   {border: 2px solid var(--secondColor); top: -30vh;}
    50%  {border: 2px solid var(--thirdColor); top: 8vh;}
    100% {border: 2px solid var(--secondColor); top: -30vh;}
}
@keyframes ball3 {
    0%   {border: 2px solid var(--secondColor); top: -30vh;}
    50%  {border: 2px solid var(--thirdColor); top: 8vh;}
    100% {border: 2px solid var(--secondColor); top: -30vh;}
}

/* circle at bottom right */
@-webkit-keyframes ball2 {
    0%   {bottom: -40vh;}
    50%  {bottom: -10vh;}
    100% {bottom: -40vh;}
}
@keyframes ball2 {
    0%   {bottom: -40vh;}
    50%  {bottom: -10vh;}
    100% {bottom: -40vh;}
}
@-webkit-keyframes ball4 {
    0%   {bottom: -40vh;}
    50%  {bottom: -10vh;}
    100% {bottom: -40vh;}
}
@keyframes ball4 {
    0%   {bottom: -40vh;}
    50%  {bottom: -10vh;}
    100% {bottom: -40vh;}
}


@-webkit-keyframes nameH1 {
    0%   {color: white;}
    25%  {color: white;}
    35%  {color: var(--secondColor);}
    
    55%  {color: var(--secondColor);}
    60%  {color:white;}
    100% {color:white;}
}


@keyframes nameH1 {
    0%   {color: white;}
    25%  {color: white;}
    35%  {color: var(--secondColor);}
    
    55%  {color: var(--secondColor);}
    60%  {color:white;}
    100% {color:white;}
}
@-webkit-keyframes lName {
    0%   {text-shadow: 1px 1px var(--secondColor);}
    25%  {text-shadow: 1px 1px var(--secondColor);}
    35%  {text-shadow: 1px 1px var(--firstColor);}
    
    55%  {text-shadow: 1px 1px var(--firstColor);}
    60%  {text-shadow: 1px 1px var(--secondColor);}
    100% {text-shadow: 1px 1px var(--secondColor);}
