@-webkit-keyframes bwFont{
    0%   {color:black;text-shadow: 0 0 1px white;}
    40%  {color:black;text-shadow: 0 0 1px white;}
    50%  {color:white;text-shadow: 0 0 1px black;}
    90%  {color:white;text-shadow: 0 0 1px black;}
    100% {color:black;text-shadow: 0 0 1px white;}
}


@keyframes bwFont{
    0%   {color:black;text-shadow: 0 0 1px white;}
    40%  {color:black;text-shadow: 0 0 1px white;}
    50%  {color:white;text-shadow: 0 0 1px black;}
    90%  {color:white;text-shadow: 0 0 1px black;}
    100% {color:black;text-shadow: 0 0 1px white;}
