#bgBody{
    min-width: 100%;
    min-height: 100%;
    object-fit: cover;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: -1;
}

#ball1{
    border: 2px solid var(--firstColor);
    position: absolute;
    z-index: -5;
    -webkit-animation-name: ball1;
            animation-name: ball1;
}
#ball3{
    background-color: var(--firstColor);
    border: 1px solid var(--secondColor);
    position: relative;
    z-index: 1;
    -webkit-animation-name: ball3;
            animation-name: ball3;
    overflow:hidden;
}
#ball1, #ball3{
    height: 70vh;
    width: 70vh;
    border-radius: 50%;
    top: -30vh;
    left: -30vh;
    -webkit-animation-duration: 40s;
            animation-duration: 40s;
    -webkit-animation-iteration-count: 999;
            animation-iteration-count: 999;
}
#ball2, #ball4{
    height: 50vh;
    width: 50vh;
    border-radius: 50%;
    bottom: -30vh;
    right: -30vh;
    z-index: -1;
    position: absolute;
    -webkit-animation-duration: 60s;
            animation-duration: 60s;
    -webkit-animation-iteration-count: 999;
            animation-iteration-count: 999;
    bottom: -28vh;
    overflow:hidden;
}   
#ball2{
    border: 2px solid var(--thirdColor);
    -webkit-animation-name: ball2;
            animation-name: ball2;
    overflow-y: visible;
    z-index: 8;
}
#ball4{
    background-color: var(--thirdColor);
    border: 2px solid var(--firstColor);
    overflow-y: clip;
    z-index: 10;
    right: -30vh;
    -webkit-animation-name: ball4;
            animation-name: ball4; 
}
#ballWrap {
    height: 100%;
    overflow-y: clip;
    position: absolute;
    right: 0;
}
/****************************
main & overarching stuff
****************************/
body {
  margin: 0;
  width: 100%;
  font-family: 'Rubik',Arial, Helvetica, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: relative;
  overflow-x: hidden;
}
#mainContent{
  width: 60%;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  overflow: clip;
  padding-top: var(--headerHeight);
}
#bg {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: all 0.3s ease-out;
  background: linear-gradient(to right, #156785 0%, #fad0c4 99%, #fad0c4 100%);
  z-index: -100;
}
.marker{
  font-family: 'Permanent Marker';
  font-weight: lighter;
}
#app{
  position: relative;
}

